
import {images} from '@/utils/images';
import {defineComponent} from 'vue';
import {CountTo as BaseCountTo} from 'vue3-count-to';
import RightApi from '@/api/RightApi'

export default defineComponent({
    name: 'unitProject',
    components: {
        BaseCountTo,
    },
    mounted() {
        RightApi.getUnitWorksNumber().then(r => {
            let data = r as any;
            this.total   = data.total;
            this.process = data.process;
            //this.percent = Number(((data.process / data.total) * 100).toFixed(0));
            this.percent = 87.8856060836647;

            // 定时任务设置
            this.tasks.push(setInterval(() => {
                this.process = 0;
            }, 10 * 1000));
            this.tasks.push(setInterval(() => {
                this.process = data.process;
            }, 10 * 1000))

            // 进度的宽度
            let inner: HTMLElement = this.$refs.waveInner as HTMLElement;
            let outer: HTMLElement = this.$refs.waveOuter as HTMLElement;
            if (inner) {
                inner.style.height = outer.offsetHeight * (this.percent / 100) + 'px';
            }
        })
    },
    data() {
        return {
            // 总数
            total: 0,
            // 进度
            process: 0,
            // 百分比
            percent: 0,
            // 定时任务
            tasks:[0],
            // 相关样式
            rotate: `background:url(${images.rotate}) no-repeat center;`,
            waveFront: `background:url(${images.waveFront}) repeat-x;`,
            waveBack: `background:url(${images.waveBack}) repeat-x;`,
            unitBg: `background:url(${images.unitBg}) no-repeat center bottom;`,
        }
    },
    // 卸载时候调用,清理下定时器
    unmounted() {
        if (this.tasks.length > 1) {
            for (let task of this.tasks) {
                if (task > 0){
                    clearInterval(task);
                }
            }
        }
    },
});
