
import {defineComponent, ref} from 'vue';
import {images} from '@/utils/images';

export default defineComponent({
    name: 'mainTitle',
    props: {
        mainTitle: {
            type: String,
        },
    },
    setup(props, {emit, attrs, slots}) {
        let background = `background:url(${images.title}) no-repeat -4px 15px`;
        let lightDot = `background:url(${images.lightDot}) no-repeat;`
        let title = ref(props.mainTitle);
        return {
            title,
            background,
            lightDot
        };
    },
});
