
import {defineComponent, ref} from "vue"
import {images} from "@/utils/images";

export default defineComponent({
    name: "subtitle ",
    props: {
        subtitle: String,
        length: Number
    },
    setup(props, {emit, attrs, slots}) {
        let img = images.subtitle
        if (props.length === 0) {
            img = images.subtitleLong
        }
        let background = `background:url(${img}) no-repeat 0px 4px`
        let title = ref(props.subtitle)
        return {
            title,
            background
        }
    }
})
