
import {defineComponent, reactive} from 'vue';
import mapTitle from '@/components/common/mapTitle.vue';
import {images} from '@/utils/images';
import {CountTo as BaseCountTo} from 'vue3-count-to';

export default defineComponent({
    name: 'onDutyToday',
    components: {
        mapTitle,
        BaseCountTo,
    },
    setup(props, {emit, attrs, slots}) {
        let data = reactive([
            {
				title: '管理人员(今日到岗)',
				now: 185,
				total: `/${200}`,
				background: `background:url(${images.manager}) no-repeat center`,
				unit: '人',
			},
			{
				title: '施工人员(今日到岗)',
				now: 1523,
				total: `/${2000}`,
				background: `background:url(${images.builders}) no-repeat center`,
				unit: '人',
			},
		]);

		setInterval(() => {
			data[0].now = 0;
			data[1].now = 0;
		}, 10 * 1000);

		setInterval(() => {
			data[0].now = 185;
			data[1].now = 1523;
		}, 10 * 1000);

		return { data };
	},
});
