
import {defineComponent, reactive} from 'vue';
import mapTitle from '@/components/common/mapTitle.vue';
import {images} from '@/utils/images';
import {CountTo as BaseCountTo} from 'vue3-count-to';

export default defineComponent({
    name: 'databaseDisplay',
    components: {
        mapTitle,
        BaseCountTo,
    },
    setup(props, {emit, attrs, slots}) {
        let data = reactive([
            {
				title: 'CPU(已使用)',
				now: 256,
				total: `/${3366}`,
				background: `background:url(${images.CPU}) no-repeat center`,
				unit: '核',
			},
			{
				title: '内存(已使用)',
				now: 4651,
				total: `/${6696.83}`,
				background: `background:url(${images.DDR}) no-repeat center`,
				unit: 'GB',
			},
			{
				title: '块存储(已使用)',
				now: 256,
				total: `/${296.91}`,
				background: `background:url(${images.HDD}) no-repeat center`,
				unit: 'TB',
			},
		]);

		setInterval(() => {
			data[0].now = 0;
			data[1].now = 0;
			data[2].now = 0;
		}, 10 * 1000);
		setInterval(() => {
			data[0].now = 256;
			data[1].now = 4651;
			data[2].now = 256;
		}, 10 * 1000);
		return { data };
	},
});
