
import {defineComponent, getCurrentInstance, onMounted, ref} from 'vue';
import subtitle from '@/components/common/subtitle.vue';
import {getDefaultLineOptions} from '@/utils/echart';
import {images} from '@/utils/images';
import {CountTo as BaseCountTo} from 'vue3-count-to';
import RightApi from '@/api/RightApi';

export default defineComponent({
    name: 'safetyMonitoring',
    components: {
        subtitle,
        BaseCountTo,
    },
    setup(props, {emit, attrs, slots}) {
        let monitoringPoint = `background:url(${images.monitoringPoint}) no-repeat center 74%;`;
        let warningPoint = `background:url(${images.warningPoint}) no-repeat center 74%;`;
        const {proxy} = getCurrentInstance() as any;
        const option = getDefaultLineOptions();
        const echarts = proxy.$echarts;
        let numbers = [1622, 13];
        let monitoring = ref(0);
        let warning = ref(0);

        const names = [
            '丽江段',
            '大理段',
            '楚雄段',
            '红河段',
        ];

        // 获取到的数据
        let data: any[] = [];

        // 当前页的固定配置
        const constOption = {
            xAxis: {
                type: 'category',
                axisLabel: {
                    color: '#7ee3fc',
                },
            },
            yAxis: {
                type: 'value',
                axisLabel: {
                    color: '#68a5d7',
                },
            },
        };

        // 合并配置
        function mergeOptions() {
            let monitors = [];
            let earlyWarnings = [];

            for (let i = 0; i < names.length; i++) {
                let datum = data.filter((item) => item['name'] == names[i]);
                monitors.push((datum.length = 1 ? datum[0].monitor : 0));
                earlyWarnings.push((datum.length = 1 ? datum[0].early_warning : 0));
            }

            return {
                ...option,
                // backgroundColor: '#000',
                backgroundColor: 'transparent',
                tooltip: {
                    show: false,
                },
                legend: {
                    show: true,
                    align: 'right',
                },
                // 网格区域
                grid: {
                    show: true,
                    // 背景颜色
                    backgroundColor: 'rgba(16, 28, 86,0.3)',
                    // 边框颜色
                    borderColor: '#fff',
                    // 边框宽度
                    borderWidth: '1',
                    bottom: 20,
                    top: 40,
                },
                //操作x轴的配置
                xAxis: {
                    ...constOption.xAxis,
                    data: data.map(item => {
                        return item.name
                    })
                },
                //操作y轴的配置
                yAxis: {...constOption.yAxis},
                //操作数据类型和数据的配置
                series: [
                    // 上半截柱子
                    {
                        name: '预警点',
                        type: 'bar',
                        barWidth: '20',
                        barGap: '-100%',
                        z: 0,
                        itemStyle: {
                            //lenged文本
                            opacity: 1,
                            color: new echarts.graphic.LinearGradient(0, 0, 1, 0,
                                [
                                    {
                                        offset: 0,
                                        color: '#fbae9d', // 0% 处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: '#ff7979', // 100% 处的颜色
                                    },
                                ],
                                false
                            )
                        },
                        data: data.map(item => item.monitor + item.early_warning),
                    },
                    // 最顶部圆片
                    {
                        name: '',
                        type: 'pictorialBar',
                        symbolSize: [20, 5],
                        symbolOffset: [0, -3],
                        z: 3,
                        symbolPosition: 'end',
                        itemStyle: {
                            color: '#ff7e7e',
                            opacity: 1,
                        },
                        data: data.map(item => item.monitor + item.early_warning),
                    },
                    //数据柱子 (蓝色柱子)
                    {
                        name: '监测点',
                        type: 'bar',
                        barWidth: 20,
                        barGap: '-100%',
                        itemStyle: {
                            //lenged文本
                            opacity: 1,
                            color: new echarts.graphic.LinearGradient(0, 0, 1, 0,
                                [
                                    {
                                        offset: 0,
                                        color: '#1dd3e5', // 0% 处的颜色
                                    },
                                    {
                                        offset: 1,
                                        color: '#1969b3', // 100% 处的颜色
                                    },
                                ],
                                false
                            )
                        },
                        data: data.map(item => item.monitor),
                    },
                    //数据圆片(蓝色柱子的顶部圆片)
                    {
                        name: '',
                        type: 'pictorialBar',
                        symbolSize: [20, 5],
                        symbolOffset: [0, -3],
                        z: 3,
                        itemStyle: {
                            opacity: 1,
                            color: 'rgb(27, 140, 255)'
                        },
                        symbolPosition: 'end',
                        data: data.map(item => item.early_warning),
                    },
                ]
            };
        }

        onMounted(() => {
            RightApi.getSubstationSafetyMonitoring().then((r) => {
                let datum = r as any;
                monitoring.value = datum.point.monitoringPoint;
                warning.value = datum.point.warningPoint;
                data = datum.stat;

                const box = proxy.$refs['chart-box'];
                const obj = echarts.init(box, 'dark');
                // 先合并配置
                obj.setOption(mergeOptions());
                // 定时器,定时处理
                setInterval(() => {
                    obj.clear();
                    obj.setOption(mergeOptions());
                }, 10 * 1000);
            });
        });

        return {monitoringPoint, warningPoint, numbers, monitoring, warning};
    },
});
