/**
 * 获取首页右上角时间
 * @return {{nowDate: string, nowWeek: string, nowTime: string}}
 */
export default function getHomeDate() {
    let
        yy = new Date().getFullYear(),
        mm = new Date().getMonth() + 1,
        dd = new Date().getDate(),
        week = new Date().getDay(),
        hh = new Date().getHours();
    let ms = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds();
    let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
    let weekArr = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
    let currentDate = yy + "-"
    if (mm >= 10) {
        currentDate += mm + "-";
    } else {
        currentDate += "0" + mm + "-";
    }
    if (dd >= 10) {
        currentDate += dd;
    } else {
        currentDate += "0" + dd;
    }
    let month = yy + '-'
    if (mm >= 10) {
        month += mm + '-01';
    } else {
        month += "0" + mm + "-01";
    }
    return {
        nowTime: hh + ':' + mf + ':' + ms,
        nowDate: yy + '/' + mm + '/' + dd,
        nowWeek: weekArr[week],
        nowDateDay: yy + '-' + mm + '-' + dd,
        addDate: yy + '-' + mm + '-' + dd,
        currentDate,
        month
    }
}

//格式化日期方法
export const DateFormat = function (date: any, fmt: string) {
    let o = {
        "M+": date.getMonth() + 1,                 //月份
        "d+": date.getDate(),                    //日
        "h+": date.getHours(),                   //小时
        "m+": date.getMinutes(),                 //分
        "s+": date.getSeconds(),                 //秒
        "q+": Math.floor((date.getMonth() + 3) / 3), //季度
        "S": date.getMilliseconds()             //毫秒
    }
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (let k in o)
        if (new RegExp("(" + k + ")").test(fmt)) { // @ts-ignore
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        }

    return fmt;
}

//遍历日期的方法
export const dealDate = function (startTime: any, endTime: any) {
    let days = [];

    //截取的开始时间
    startTime = new Date(startTime.split(' ')[0]);

    //截取的结束时间
    endTime = new Date(endTime.split(' ')[0]);

    //利用setTime获取两个日期之间差值,差值毫秒换算成天1000*60*60*24
    let distanceDayLength = (endTime.getTime() - startTime.getTime()) / (1000 * 60 * 60 * 24);
    let startDay = startTime.getDate();
    for (let i = 0; i <= distanceDayLength; i++) {
        days.push(DateFormat(new Date(startTime.setDate(startDay + i)), "yyyy-MM-dd"));
    }

    return days;
}

