
import {defineComponent} from "vue"
import {images} from "@/utils/images";

export default defineComponent({
    name: "bubble ",
    setup(props, {emit, attrs, slots}) {
        let background = `background:url(${images.bubbleBj}) no-repeat bottom center;`
        let b1 = `background:url(${images.b1}) no-repeat center 100%;`
        let b2 = `background:url(${images.b2}) no-repeat center 100%;`
        let b3 = `background:url(${images.b3}) no-repeat center 100%;`
        let b4 = `background:url(${images.b4}) no-repeat center 100%;`
        let b5 = `background:url(${images.b5}) no-repeat center 100%;`
        let b6 = `background:url(${images.b6}) no-repeat center 100%;`
        let b7 = `background:url(${images.b7}) no-repeat center 100%;`
        let aaa = `background:url(${images.rotate}) no-repeat center 100%;`
        return {background, b1, b2, b3, b4, b5, b6, b7, aaa}
    }
})
