
import {defineComponent, nextTick} from "vue"
import subtitle from "@/components/common/subtitle.vue";
import {images} from "@/utils/images";
// let subtitle = '一期投资情况'
// const progress = ref(null)
// nextTick(() => {
//     progress.value.style.width = '24px'
// })

export default defineComponent({
    name: "tranche",
    components: {
        subtitle
    },
    mounted() {
        nextTick(() => {
            let sumWidth: HTMLElement = this.$refs.totalBj as HTMLElement
            let totalProgress: HTMLElement = this.$refs.total_progress as HTMLElement
            let total_p: HTMLElement = this.$refs.total_p as HTMLElement
            let totalBj: HTMLElement = this.$refs.totalBj as HTMLElement
            let total_tooltip: HTMLElement = this.$refs.total_tooltip as HTMLElement
            let total_line: HTMLElement = this.$refs.total_line as HTMLElement
            totalProgress.style.width = totalBj.offsetWidth * (this.percent / 100) + 'px';
            total_p.style.left = sumWidth.offsetWidth * (this.percent / 100) + 6 + 'px';
            total_tooltip.style.left = sumWidth.offsetWidth * (this.percent / 100) - total_tooltip.offsetWidth / 2 + 'px'
            let year_progress: HTMLElement = this.$refs.year_progress as HTMLElement
            let year_p: HTMLElement = this.$refs.year_p as HTMLElement
            let yearBj: HTMLElement = this.$refs.yearBj as HTMLElement
            let year_tooltip: HTMLElement = this.$refs.year_tooltip as HTMLElement
            year_tooltip.style.left = sumWidth.offsetWidth * (this.year_percent / 100) - year_tooltip.offsetWidth / 2 + 'px'
            year_progress.style.width = yearBj.offsetWidth * (this.year_percent / 100) + 'px';
            year_p.style.left = sumWidth.offsetWidth * (this.year_percent / 100) + 6 + 'px';
        })
    },
    setup(props, {emit, attrs, slots}) {
        let total_investment: number = 826
        let accomplish: number = 478
        let percent = Math.round(accomplish / total_investment * 100)
        let year_investment: number = 133
        let year_accomplish: number = 73
        let year_percent = Math.round(year_accomplish / year_investment * 100)
        console.log(year_percent)
        let tooltipBj = `background:url(${images.tooltip}) no-repeat center;`
        return {
            total_investment,
            accomplish,
            percent,
            year_investment,
            year_accomplish,
            year_percent,
            tooltipBj,
        }
    },
})
