import * as Mock from "mockjs";

import RightApiMock from "@/plugin/mock/data/RightApiMock";
import LeftApiMock from "@/plugin/mock/data/LeftApiMock";


export interface Method {
    url: string,
    type: string
    response: any
}

const mocks = [...RightApiMock,...LeftApiMock]

// 载入
for (let datum of mocks) {
    Mock.mock('http://localhost:8080' + datum.url, datum.type, datum.response)
}

export default Mock;
