
import { defineComponent, onMounted, reactive } from 'vue';
import { images } from '@/utils/images';
import rightDetails from '@/views/rightDetails.vue';
import mapDetails from '@/views/mapDetails.vue';
import leftDetails from '@/views/leftDetails.vue';
import bubbleAE from '@/components/AE/bubbleAE.vue';
import DateUtils, { DateFormat } from '@/utils/DateUtils';
import { getKunMingWeather } from '@/api/WeatherApi';

export default defineComponent({
	name: 'App',
	components: {
		rightDetails,
		mapDetails,
		leftDetails,
		bubbleAE,
	},
	created() {
		// 定时器,用来更新时间
		setInterval(() => {
			let now = new Date();
			this.date = DateFormat(now, 'yyyy/MM/dd');
			this.time = DateFormat(now, 'hh:mm:ss');
			let { nowWeek } = DateUtils();
			this.week = nowWeek;
		}, 1000);
	},
	setup(props, { emit, attrs, slots }) {
		let KMWeather = reactive({ city: '', weather: '', temperature: '' });
		let background = `background:url(${images.background}) no-repeat center;background-size:100%;height:100%`;
		let header = {
			background: `background:url(${images.header}) no-repeat center  ;`,
			logo: `background:url(${images.logo}) no-repeat center;background-size:100%`,
			title: '云南省滇中引水工程',
		};
		let footer = `background:url(${images.footer}) no-repeat center;background-size:100%;`;
		onMounted(() => {
			getKunMingWeather().then((res) => {
				let obj = {};
				let { city, weather, temperature } = res || {};
				obj = {
					city: city,
					weather: weather,
					temperature: temperature,
				};
				Object.assign(KMWeather, obj);
			});

			setInterval(() => {
				getKunMingWeather().then((res) => {
					let obj = {};
					let { city, weather, temperature } = res || {};
					obj = {
						city: city,
						weather: weather,
						temperature: temperature,
					};
					Object.assign(KMWeather, obj);
				});
			}, 600 * 1000);
		});
		return { background, header, footer, KMWeather };
	},
	data() {
		return {
			// 日期
			date: '',
			// 时间
			time: '',
			// 星期
			week: '',
			// 地址
			addr: '',
			// 温度
			oC: '',
			// 天气
			weather: '',
		};
	},
});
