
import {defineComponent, nextTick} from "vue"
import subtitle from "@/components/common/subtitle.vue";
import {images} from "@/utils/images";
import {DateFormat} from '@/utils/DateUtils';


export default defineComponent({
    name: "tranche",
    components: {
        subtitle
    },
    data() {
        return {
            accomplish: DateFormat(new Date(), 'yyyy-MM-dd'),
            percent: 38,
            date_percent: 0,
            tooltipBj: `background:url(${images.tooltip2}) no-repeat center bottom;`,
            // 预计完成日期
            estimated_complete: '2026-10-10'
        }
    },
    created() {
        this.sumPercentage();
    },
    mounted() {
        nextTick(() => {
            let sumWidth: HTMLElement = this.$refs.totalBj as HTMLElement;
            let totalProgress: HTMLElement = this.$refs.total_progress as HTMLElement;
            let total_p: HTMLElement = this.$refs.total_p as HTMLElement;
            let totalBj: HTMLElement = this.$refs.totalBj as HTMLElement;
            let total_tooltip: HTMLElement = this.$refs.total_tooltip as HTMLElement;
            let start_box: HTMLElement = this.$refs.start_box as HTMLElement

            // 总长度
            totalProgress.style.width = totalBj.offsetWidth * (this.percent / 100) + 'px';
            // 进度条百分比
            total_p.style.left = sumWidth.offsetWidth * (this.percent / 100) + 6 + 'px';
            // 进度条上方日期百分比
            console.log(this.date_percent / 100)
            console.log(123131231)
            console.log(sumWidth.offsetWidth)
            console.log(sumWidth.offsetWidth * (this.date_percent / 100))
            total_tooltip.style.left = sumWidth.offsetWidth * (this.date_percent / 100) + start_box.offsetWidth + 'px'
            // @formatter:on
        })
    },
    methods: {
        sumDiffDay(d1: string, d2: string): number {
            let total, diff;
            let startData = new Date(d1);
            let endData = new Date(d2);
            diff = Math.abs(startData.getTime() - endData.getTime());
            total = Math.floor(diff / (1000 * 3600 * 24));
            return total;
        },
        // 计算百分比
        sumPercentage() {
            let totalDay = this.sumDiffDay('2018-10-1', '2026-10-1');
            console.log(totalDay)
            let nowDay = this.sumDiffDay('2018-10-1', DateFormat(new Date(), 'yyyy-MM-dd'));
            console.log(nowDay)
            let res = Number(((nowDay / totalDay) * 100).toFixed(0));
            console.log(res)
            console.debug('总计时间:', totalDay, '开始到现在:', nowDay);
            console.debug('百分比  :', res);
            this.date_percent = res;
        }
    }
})
