import axios from "axios";

let url = 'https://restapi.amap.com/v3/weather/weatherInfo?key=1e08b6fefb266be41629cc6a22c3c29a'

export function getKunMingWeather() {
    return axios.get(`${url}&city=${530100}`).then(res => {
        let {status, data} = res || {}
        if (status === 200) {
            let {status, lives} = data || {}
            if (status === "1" && lives) {
                return lives[0]
            }
        }
    })
}